
body {
    padding: 0;
    margin: 0;
}















#tab-bar {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  background-color: #f6f6f6;

  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;

  -webkit-box-shadow: 0px -1px 1px rgba(0,0,0,.1);

          box-shadow: 0px -1px 1px rgba(0,0,0,.1);
}
.tab-bar-item {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-align: center;
  height: 49px;   /* 補充:一般tabbar設計都是49px的高度 */ 
  font-weight: 300;
}
.tab-bar-item img {
  width: 24px;
  height: 24px;
  margin: 3px 0 0;
  vertical-align: middle;
}

















































